//员工管理
import http from '../httpConfig'

const table = "mrbase_sys_staff_property_group"


export const getOrganizationalManagementList = (params = {}) => {
  params['~table~'] = table
  return new Promise((resolve, reject) => {
    http.post('/api/staff-property/find-group/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}

